export default ()=> {
      return (
        <div className="question" data-index="75" dangerouslySetInnerHTML = {{ __html: `
<h6>76. 输出什么?</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> <span class="token punctuation">{</span> name<span class="token operator">:</span> myName <span class="token punctuation">}</span> <span class="token operator">=</span> <span class="token punctuation">{</span> name<span class="token operator">:</span> <span class="token string">"Lydia"</span> <span class="token punctuation">}</span><span class="token punctuation">;</span>

console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span>name<span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: <code>&quot;Lydia&quot;</code></li>
<li>B: <code>&quot;myName&quot;</code></li>
<li>C: <code>undefined</code></li>
<li>D: <code>ReferenceError</code></li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: D</h4>
<p>当我们从右侧的对象解构属性<code>name</code>时，我们将其值<code>Lydia</code>分配给名为<code>myName</code>的变量。</p>
<p>使用<code>{name：myName}</code>，我们是在告诉JavaScript我们要创建一个名为<code>myName</code>的新变量，并且其值是右侧对象的<code>name</code>属性的值。</p>
<p>当我们尝试打印<code>name</code>，一个未定义的变量时，就会引发<code>ReferenceError</code>。</p>
</p>
</details>
` }}></div>
      );
    }